
.btn-primary {
    font-weight: 600;
    border-color: #6647b6;
}
.engage .cards{
    font-weight: 600;
    border-radius: 15px;
    background: rgb(49, 54, 72);
}

.cards .social-details .social-icons .ui.divider>.icon {
    color: #ccc;
}

.cards .ui.toggle.checkbox .box:before, .ui.toggle.checkbox label:before{
    background: #ccc;
}

.react-multi-carousel-item{
    padding-left: 3px;
    padding-right: 3px;
}


.react-multiple-carousel__arrow:hover,.react-multiple-carousel__arrow--left,.react-multiple-carousel__arrow--right{
    background: transparent;
    outline: none;
}


.share-card-details .share-info{
    font-size: 20px;
    font-weight: 500;
}

.share-card-details .buttons .ui.button {
    border-radius: 1.3em;
}

@media only screen and (max-width: 767px) {
    .share-card-details  { width: 70% !important;}
}

.users-around .user .user-icon{
    font-size: 18px;
    color: #ccc;
}

.users-around .user .name {
    font-size: 18px;
}

.engage{
    color:#ccc;
}
